import React, { Fragment } from 'react';
// My components
import MainTitle from '../styled/MainTitle';
import Wrapper from '../styled/Wrapper';
import FormationItems from './FormationItems';
// External librairies
import styled from 'styled-components';

const Content = styled.div`
  height: calc(100vh - 200px - 5%);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 5%;
  padding: 0 5%;
`;

const Card = styled.div`
  width: 100%;
  height: 100%;
  background-size: cover;
  position: relative;
  transition: all ease-in-out .5s;
  &:hover {
    transform: scale(1.1);
    transition: all ease-in-out .5s;
  }
`;

const Filter = styled.div`
  position: relative;
  text-align: center;
  align-self: center;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(250, 250, 250, 0.4);
`;

const HoverFilter = styled.div`
  position: relative;
  text-align: center;
  align-self: center;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1003;
  transition: all ease-in-out .5s;
  p {
    color: #fff;
    transition: all ease-in-out .5s;
  }
  &:hover {
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 1;
    transition: all ease-in-out .5s;
    p {
      padding-top: 30%;
      transition: all ease-in-out .5s;
    }
  }
`;

const Cadre = styled.div`
  height: 70%;
  width: 70%;
  position: absolute;
  border: 4px solid #fff;
  left: 15%;
  top: 15%;
`;

const Logo = styled.img`
  width: 55%;
  height: 100%;
  object-fit: contain;

`;

const Formations = ({ language, onLanguageClick }) => {

  console.log(FormationItems);

  const rendereFormations = FormationItems.map((item) => {
    return (
      <Card style={{backgroundImage: `url(${process.env.PUBLIC_URL}/${item.img})`}}>
        <HoverFilter>
          <p>Une description</p>
        </HoverFilter>
        <Filter>
          <Cadre/>
          <Logo src={process.env.PUBLIC_URL + `/${item.logo}`} alt={item.label}/>
        </Filter>
      </Card>
    );
  })

  const title = language ==='fr' ? 'Mes Formations' : 'Education'

  return (
    <Wrapper style={{backgroundColor: '#f8f8f8'}}>
      <MainTitle>{title}</MainTitle>
      <Content>
        {rendereFormations}
      </Content>
    </Wrapper>
  );
};

export default Formations;
